import moment from 'moment'
import React from 'react';

export function typeMasterFormatter(cell) {
    switch (cell) {
        case 'TOP_LEADER':
            return "  ";
        case 'TOP':
            return " ";
        case 'MIDDLE_PLUS':
            return "  ";
        case 'MIDDLE':
            return " ";
        case 'START':
            return " ";
        case 'SUPPORT':
            return "";
        case 'TOP_COLORIST':
            return " ";
        case 'COLORIST':
            return "";
        case 'CARE_MASTER':
            return "   ";
        default:
            return "";
    }
}

export function typeHairFormatter(cell) {
    switch (cell) {
        case 'HAIR_EXTENSION':
            return " ";
        case 'HAIR_REMOVAL':
            return " ";
        default:
            return "";
    }
}

export function typeMasterWorkingDayFormatter(cell) {
    switch (cell) {
        case '$2X2':
            return "2  2";
        case '$5X2':
            return "5  2";
        case '$4X2':
            return "4  2";
        default:
            return "";
    }
}

export function phoneFormatter(cell, row) {
    if (row.person.phone) {
        return (
            <span>
                {phoneFormatterToString(row.person.phone)}
            </span>
        );
    }

    return (
        <span>$ { cell } NTD</span>
    );
}

export function phoneFormatterToString(phone) {
    return '+' + phone.substring(0,1)+' ('
        + phone.substring(1,4) + ') '
        + phone.substring(4, 7) + '-'
        + phone.substring(7, 9) + '-'
        + phone.substring(9, 11);
}

export function dateFormatter(cell) {
    return (
        <span>{moment.unix(cell).toDate().toLocaleDateString()}</span>
    );
}
